  align-items: center;
  width: 180px;
  height: 300px;
  display: flex;
  overflow: hidden;
}

.event-card img {
  object-fit: cover;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
  width: 180px;
  height: 227px;
}

.event-info {
  text-align: center;
  color: #fff;
  padding: 5px 10px;
  font-size: 13px;
}

.event-info strong {
  color: #dc56c5;
  margin-bottom: 5px;
  display: block;
}

footer {
  color: #aaa;
  text-align: center;
  background: #111;
